'use client';

import { useState, useEffect, useRef } from 'react';

interface PerformanceMetrics {
  memoryUsage: number;
  cpuUsage: number;
  renderTime: number;
  domNodes: number;
  eventListeners: number;
}

/**
 * ÊÄßËÉΩÁõëÊéßÁªÑ‰ª∂
 * Áî®‰∫éÁõëÊéßÂíåÊòæÁ§∫Â∫îÁî®ÊÄßËÉΩÊåáÊ†á
 */
const PerformanceMonitor = () => {
  const [metrics, setMetrics] = useState<PerformanceMetrics>({
    memoryUsage: 0,
    cpuUsage: 0,
    renderTime: 0,
    domNodes: 0,
    eventListeners: 0
  });
  const [isVisible, setIsVisible] = useState(false);
  const [isMonitoring, setIsMonitoring] = useState(false);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);
  const renderStartTime = useRef<number>(0);

  // Ëé∑ÂèñÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
  const getMemoryUsage = (): number => {
    if ('memory' in performance) {
      const memory = (performance as any).memory;
      return Math.round(memory.usedJSHeapSize / 1024 / 1024); // MB
    }
    return 0;
  };

  // Ëé∑ÂèñDOMËäÇÁÇπÊï∞Èáè
  const getDOMNodeCount = (): number => {
    return document.querySelectorAll('*').length;
  };

  // ‰º∞ÁÆó‰∫ã‰ª∂ÁõëÂê¨Âô®Êï∞ÈáèÔºàÁÆÄÂåñÁâàÊú¨Ôºâ
  const getEventListenerCount = (): number => {
    // ËøôÊòØ‰∏Ä‰∏™ÁÆÄÂåñÁöÑ‰º∞ÁÆóÔºåÂÆûÈôÖÊï∞ÈáèÂèØËÉΩÊõ¥Â§ö
    const elements = document.querySelectorAll('*');
    let count = 0;
    
    // Ê£ÄÊü•Â∏∏ËßÅÁöÑ‰∫ã‰ª∂ÁõëÂê¨Âô®
    elements.forEach(element => {
      const events = ['click', 'mousedown', 'mouseup', 'mouseover', 'mouseout', 'keydown', 'keyup', 'scroll', 'resize'];
      events.forEach(event => {
        if ((element as any)[`on${event}`]) {
          count++;
        }
      });
    });
    
    return count;
  };

  // ÊµãÈáèÊ∏≤ÊüìÊó∂Èó¥
  const measureRenderTime = () => {
    renderStartTime.current = performance.now();
    
    requestAnimationFrame(() => {
      const renderTime = performance.now() - renderStartTime.current;
      setMetrics(prev => ({ ...prev, renderTime: Math.round(renderTime * 100) / 100 }));
    });
  };

  // Êõ¥Êñ∞ÊÄßËÉΩÊåáÊ†á
  const updateMetrics = () => {
    const newMetrics: PerformanceMetrics = {
      memoryUsage: getMemoryUsage(),
      cpuUsage: 0, // CPU‰ΩøÁî®ÁéáËæÉÈöæÂáÜÁ°ÆÊµãÈáèÔºåÊöÇÊó∂ËÆæ‰∏∫0
      renderTime: metrics.renderTime,
      domNodes: getDOMNodeCount(),
      eventListeners: getEventListenerCount()
    };
    
    setMetrics(newMetrics);
    measureRenderTime();
  };

  // ÂºÄÂßã/ÂÅúÊ≠¢ÁõëÊéß
  const toggleMonitoring = () => {
    if (isMonitoring) {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
      setIsMonitoring(false);
    } else {
      updateMetrics();
      intervalRef.current = setInterval(updateMetrics, 2000); // ÊØè2ÁßíÊõ¥Êñ∞‰∏ÄÊ¨°
      setIsMonitoring(true);
    }
  };

  // Ê∏ÖÁêÜ
  useEffect(() => {
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, []);

  // Ëé∑ÂèñÊÄßËÉΩÁ≠âÁ∫ßÈ¢úËâ≤
  const getPerformanceColor = (value: number, thresholds: { good: number; warning: number }) => {
    if (value <= thresholds.good) return 'text-green-400';
    if (value <= thresholds.warning) return 'text-yellow-400';
    return 'text-red-400';
  };

  if (!isVisible) {
    return (
      <button
        onClick={() => setIsVisible(true)}
        className="fixed bottom-4 left-4 bg-gray-800 text-white p-2 rounded-full shadow-lg hover:bg-gray-700 z-50"
        title="ÊòæÁ§∫ÊÄßËÉΩÁõëÊéß"
      >
        üìä
      </button>
    );
  }

  return (
    <div className="fixed bottom-4 left-4 bg-gray-900 border border-gray-700 rounded-lg p-4 max-w-sm z-50 text-sm">
      <div className="flex justify-between items-center mb-3">
        <h3 className="text-white font-bold">ÊÄßËÉΩÁõëÊéß</h3>
        <div className="flex gap-2">
          <button
            onClick={toggleMonitoring}
            className={`px-2 py-1 rounded text-xs ${
              isMonitoring 
                ? 'bg-red-600 text-white hover:bg-red-700' 
                : 'bg-green-600 text-white hover:bg-green-700'
            }`}
          >
            {isMonitoring ? 'ÂÅúÊ≠¢' : 'ÂºÄÂßã'}
          </button>
          <button
            onClick={() => setIsVisible(false)}
            className="px-2 py-1 bg-gray-600 text-white rounded text-xs hover:bg-gray-700"
          >
            ÈöêËóè
          </button>
        </div>
      </div>

      <div className="space-y-2">
        <div className="flex justify-between">
          <span className="text-gray-300">ÂÜÖÂ≠ò‰ΩøÁî®:</span>
          <span className={getPerformanceColor(metrics.memoryUsage, { good: 50, warning: 100 })}>
            {metrics.memoryUsage} MB
          </span>
        </div>

        <div className="flex justify-between">
          <span className="text-gray-300">Ê∏≤ÊüìÊó∂Èó¥:</span>
          <span className={getPerformanceColor(metrics.renderTime, { good: 16, warning: 33 })}>
            {metrics.renderTime} ms
          </span>
        </div>

        <div className="flex justify-between">
          <span className="text-gray-300">DOMËäÇÁÇπ:</span>
          <span className={getPerformanceColor(metrics.domNodes, { good: 1000, warning: 2000 })}>
            {metrics.domNodes}
          </span>
        </div>

        <div className="flex justify-between">
          <span className="text-gray-300">‰∫ã‰ª∂ÁõëÂê¨Âô®:</span>
          <span className={getPerformanceColor(metrics.eventListeners, { good: 50, warning: 100 })}>
            {metrics.eventListeners}
          </span>
        </div>
      </div>

      <div className="mt-3 pt-2 border-t border-gray-700">
        <div className="text-xs text-gray-400">
          <div>üü¢ ËâØÂ•Ω üü° Ë≠¶Âëä üî¥ Âç±Èô©</div>
          <div className="mt-1">
            Áä∂ÊÄÅ: {isMonitoring ? 'ÁõëÊéß‰∏≠...' : 'Â∑≤ÂÅúÊ≠¢'}
          </div>
        </div>
      </div>

      <div className="mt-2">
        <button
          onClick={updateMetrics}
          className="w-full px-2 py-1 bg-blue-600 text-white rounded text-xs hover:bg-blue-700"
        >
          Á´ãÂç≥Êõ¥Êñ∞
        </button>
      </div>
    </div>
  );
};

export default PerformanceMonitor;
